import { Controller, Commands, Block } from '@ali/4ever-cangjie';
import removeLink from './removeLink';
import { BlockLinkCard, } from '../../types';


export default function convertBlockLinkCardToTextLink(
  controller: Controller,
  node: BlockLinkCard,
) {
  // 这里需要先插入，再删除，否则会报找不到 path 的异常
  controller.command(Commands.moveToEndOfNode, node)
    .command(Commands.insertBlock, {
      type: Block.DEFAULT_TYPE,
    })
    .command(removeLink, node);


  return controller;
};

